.animate-content {
  width: 100%;
  max-width: 600px;
  padding: 100px 0;
  margin: 0 auto;
  background: #333;
}
.animate-content .animate-box {
  width: 231px;
  height: 266px;
  margin: 0 auto;
  background: url("../../../assets/images/block1.png") no-repeat center;
  background-size: contain;
  position: relative;
}
.animate-content .animate-box img {
  position: absolute;
  -webkit-animation: 9s infinite ease-out paused;
  animation: 9s infinite ease-out paused;
  margin: 0 !important;
}
.animate-content .animate-box img:nth-child(1) {
  left: -50px;
  top: 10px;
  -webkit-animation-name: linego1;
  animation-name: linego1;
}
.animate-content .animate-box img:nth-child(2) {
  left: 179px;
  top: 10px;
  -webkit-animation-name: linego2;
  animation-name: linego2;
}
.animate-content .animate-box img:nth-child(3) {
  left: 65px;
  top: 207px;
  -webkit-animation-name: linego3;
  animation-name: linego3;
}
@-webkit-keyframes linego1 {
  0% {
    left: -50px;
    top: 10px;
  }
  16.7% {
    left: 65px;
    top: -56px;
  }
  33.3% {
    left: 179px;
    top: 10px;
  }
  50% {
